@use 'base/mixins' as m;
@use 'base/variables' as v;

.bar {
    display: flex;
    flex-direction: column;
    padding-top: 3rem;

    @include m.tablet{
        flex-direction: row;
        justify-content: space-between;
    }
}

.navegation {

    @include m.tablet{
        display: flex;
        align-items: center;
    }

    a{
        color: v.$white;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        @include m.tablet{
            margin-right: 2rem;
            font-size: 1.8rem;

            &:last-of-type{
                margin-right: 0;
            }
        }
    }

}